// Importing Reset File
@import '_reset';

// Start Coding From Here
body {
   padding-right: 0!important;
}

.navbar{
   .navbar-brand {
      font-family: 'ZCOOL KuaiLe', cursive;
   }
   .nav-link{
      font-family: 'Big Shoulders Text', cursive;
      font-weight: 900;
      padding: 0 15px;
      &.userImage{
         img {
            width: 40px;
            height: 40px;
            border-radius: 50%;
            box-shadow: 1px 1px 5px rgba($color: #000, $alpha: 0.3);
            padding: 2px;
            background: #ccc;
         }
      }
   }
   i{
      background: #ccc;
      color: #fff;
      padding: 5px 3px 4px 5px;
      border-radius: 50%;
      cursor: pointer;
      box-shadow: 1px 1px 5px rgba($color: #000, $alpha: 0.3);
      text-align: center;
      margin-top: 15px;
      position: relative;
      bottom: 7px;
      &:hover{
         color: #000;
         background: #fff;
      }
   }
}
.header {
   color: #fff;
   font-family: 'Big Shoulders Text', cursive;
   text-align: center;
   @include fixedBack("back.jpg");
   &.Auth {
      .overlay {
         padding: 60px 0 250px;
         .tasksBox{
            padding: 25px;
            background: #fff;
            margin: 25px auto;
            .head {
               input {
                  width: 70%;
                  margin: 15px 0;
                  display: inline-block;
                  border-radius: 7px 0 0 7px;
                  @include xs {
                     width: 60%;
                  }
               }
               button {
                  width: 25%;
                  display: inline-block;
                  border-radius: 0 7px 7px 0;
                  padding: 11.5px;
                  border: 1.5px solid #002147;
                  &:hover {
                     transform: none;
                     background: crimson;
                     border-color: crimson;
                  }
                  @include xs {
                     width: 35%;
                     padding: 11px;
                  }
               }
            }
            .task {
               border-left: 3px solid #ccc;
               background: #f6f6f6;
               padding: 15px;
               text-align: left;
               position: relative;
               i {
                  position: absolute;
                  top: 50%;
                  right: 15px;
                  cursor: pointer;
                  transform: translateY(-50%);
                  &:nth-child(2){
                     right: 35px;
                  }
                  &:hover{
                     color: #000;
                  }
               }
            }
         }
         .profileEdits {
            padding: 25px;
            background: #fff;
            margin: 25px auto;
            color: #000;
            img {
               width: 150px;
               height: 150px;
               display: block;
               margin: 15px auto;
               border-radius: 50%;
               padding: 5px;
               background: #ccc;
               box-shadow: 0 2px 6px rgba($color: #000, $alpha: 0.3);
               cursor: pointer;
            }
            h2{
               text-align: left;
               margin: 20px 10px;
               background: #f6f6f6;
               padding: 15px;
               border-left: 4px solid #ccc;
               position: relative;
               span {
                  font-size: inherit;
                  color: #aaa;
               }
               i{
                  position: absolute;
                  top: 50%;
                  right: 15px;
                  cursor: pointer;
                  font-size: 20px;
                  transform: translateY(-50%);
                  &:hover{
                     color: #002147;
                  }
               }
            }
         }
      }
   }
   .overlay {
      @include overlay(#000, 0.85);
      padding: 145px 0;
      
      h1 {
         font-family: 'ZCOOL KuaiLe', cursive;
      }
      .box{
         margin-top: 75px;
         padding: 20px;
         background: rgba($color: #fff, $alpha: 0.2);
         box-shadow: 1px 4px 6px rgba($color: #000, $alpha: 0.3);
         font-family: 'Big Shoulders Text', cursive;
         text-align: center;
         border-radius: 5px;
         cursor: pointer;
         &:hover {
            transform: translateY(-10px);
            box-shadow: 1px 10px 12px rgba($color: #000, $alpha: 0.3);
            background: #f6f6f6;
            h4 {
               color: #000;
            }
         }
         img {
            width: 80px;
            height: 80px;
         }
      }

      .loginBox{
         padding: 25px;
         background: #fff;
         margin: 0 auto;
         width: 60%;
         text-align: center;
         box-shadow: 1px 10px 12px rgba($color: #000, $alpha: 0.3);
         h1 {
            color: #000;
         }
         input {
            width: 90%;
            margin: 0 auto 10px;
            display: block;
         }
         label {
            color: #000;
            margin-top: 15px;
         }
         button {
            display: block;
            margin: 15px auto;
            padding: 8px 50px;
            border-radius: 5px;
            background: #000;
         }
      }
   }
}

